import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocVacationRequest,
  ILocVacationResponse,
} from "../../models/localization-vacation";

@Injectable({
  providedIn: "root",
})
export class LocVacationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILocVacationRequest) {
    return this.post<IResponse<ILocVacationResponse>>(
      "VacationTypeLoc/Create",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocVacationRequest) {
    return this.post<IResponse<ILocVacationResponse>>(
      "VacationTypeLoc/Edit",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponse<ILocVacationResponse[]>>("VacationTypeLoc/GetList", {
      params: {
        ...params,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  getById(id: number) {
    return this.get<IResponse<ILocVacationResponse[]>>("VacationTypeLoc/GetAll", {
      params: {
        $filter: `id eq ${id}`,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  getAll() {
    return this.get<IResponse<ILocVacationResponse[]>>("VacationTypeLoc/GetAll", {
      params: this._paramsService.versionCountryParams,
    });
  }

  Delete(id: number) {
    return this.delete<IResponse>("VacationTypeLoc/Delete", {
      params: { Id: id, ...this._paramsService.versionCountryParams },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
