import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  LocAddVersionComponent
} from "app/system-setup/localization-setup/components/localization-version/localization-add-version/loc-add-version.component";
import {
  LocVersionIndexComponent
} from "app/system-setup/localization-setup/components/localization-version/localization-version-index/loc-version-index.component";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { LocPenaltiesComponent } from "app/system-setup/localization-setup/components/localization-penalties/loc-penalties.component";
import {
  LocViolationsIndexComponent
} from "app/system-setup/localization-setup/components/localization-violations/localization-violations-index/loc-violations-index.component";
import {
  LocAddViolationComponent
} from "app/system-setup/localization-setup/components/localization-violations/localization-add-violation/loc-add-violation.component";
import {
  LocPenaltyRegulationsIndexComponent
} from "app/system-setup/localization-setup/components/localization-penalty-regulations/localization-penalty-regulations-index/loc-penalty-regulations-index.component";
import {
  LocAddPenaltyRegulationsComponent
} from "app/system-setup/localization-setup/components/localization-penalty-regulations/localization-add-penalty-regulations/loc-add-penalty-regulations.component";
import {
  LocViolationTypesComponent
} from "app/system-setup/localization-setup/components/localization-violation-types/loc-violation-types.component";
import {
  LocVacationIndexComponent
} from "app/system-setup/localization-setup/components/localization-vacation/localization-vacation-index/loc-vacation-index.component";
import {
  LocVacationTypeComponent
} from "app/system-setup/localization-setup/components/localization-vacation/localization-vacation-type/loc-vacation-type.component";

const routes: Routes = [
  {
    path: "version",
    data: {view: ViewsManager.version},
    canActivate: [CheckIsViewAllowedGuard],
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index", // version/index
        component: LocVersionIndexComponent,
        data: {view: ViewsManager.version_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add", // version/add
        component: LocAddVersionComponent,
        data: {view: ViewsManager.add_version},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id", // version/edit/1
        component: LocAddVersionComponent,
        data: {view: ViewsManager.add_version},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "penalties",
    component: LocPenaltiesComponent,
    data: {view: ViewsManager.localization_penalties},
    canActivate: [CheckIsViewAllowedGuard],
  },

  {
    path: "violation-types",
    component: LocViolationTypesComponent,
    data: {view: ViewsManager.localization_violation_types},
    canActivate: [CheckIsViewAllowedGuard],
  },

  {
    path: "violations",
    data: {view: ViewsManager.localization_violations},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: LocViolationsIndexComponent,
        data: {view: ViewsManager.localization_violations_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: LocAddViolationComponent,
        data: {view: ViewsManager.localization_add_violation},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: LocAddViolationComponent,
        data: {view: ViewsManager.localization_add_violation},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "penalty-regulations",
    data: {view: ViewsManager.localization_penalty_regulations},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: LocPenaltyRegulationsIndexComponent,
        data: {view: ViewsManager.localization_penalty_regulations_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: LocAddPenaltyRegulationsComponent,
        data: {view: ViewsManager.localization_add_penalty_regulations},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: LocAddPenaltyRegulationsComponent,
        data: {view: ViewsManager.localization_add_penalty_regulations},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "vacations",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: LocVacationIndexComponent,
        data: {view: ViewsManager.localization_vacation_index},
        canActivate: [CheckIsViewAllowedGuard],
      },

      {
        path: "add",
        component: LocVacationTypeComponent,
        data: {view: ViewsManager.localization_add_vacation},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:vacationId",
        component: LocVacationTypeComponent,
        data: {view: ViewsManager.localization_add_vacation},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LocalizationSetupRoutingModule {}
