import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { LocVersionService } from "app/system-setup/localization-setup/services/localization-version.service";
import { ILocVersionModel } from "app/system-setup/localization-setup/models/localization-version";
import { HelpersFunctions, msgTranslate, } from "app/shared/resources/helpers-functions";

@Component({
  selector: "app-version-index",
  templateUrl: "./loc-version-index.component.html",
  styleUrls: ["./loc-version-index.component.scss"],
})
export class LocVersionIndexComponent
  extends AppHelpers
  implements OnInit {
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ILocVersionModel>;
  mandatoryColomns: string[] = [
    "Ser",
    "name",
    "date",
    "active",
    "note",
    "countryName",
    "actions",
  ];
  optionalColomns: string[] = [];

  constructor(public versionService: LocVersionService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  override onChangeVersionOrCountry() {
    this.onChangeBusinessAndCompanyId();
  }

  ngOnInit(): void {}

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    this.removePageParams();
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.versionService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ILocVersionModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          res.data.map((e) => {
            e.countryName = msgTranslate(
              e.country?.ar_Name,
              e.country?.en_Name
            );
            e.date = HelpersFunctions.mapDate(e.date);
          });
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res.message.messages);
      });
  }

  delete(model: ILocVersionModel) {
    this.showLoading();
    this.versionService.Delete(Number(model.id)).subscribe((res: IResponse) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          this.getList(this.currentPage, this.page.pageSize);
        }
        this.deleteSuccessfullyMsg(res.message.messageTypeName);
      }
      else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  onDelete(model: ILocVersionModel) {
    let msg = this.msgTranslate(model.name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(model);
    });
  }
}
