import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { locPenaltyRegDetailsToMap } from "app/system-setup/localization-setup/mapper/localization-penalty-regulation";
import { ILocPenaltyModel } from "app/system-setup/localization-setup/models/localization-penalties";
import {
  ILocEditPenaltyRegulationDetailsRequest,
  ILocPenaltyRegulationDetailsDeleteParams,
  ILocPenaltyRegulationDetailsModel,
  ILocPenaltyRegulationDetailsRequest,
} from "app/system-setup/localization-setup/models/localization-penalty-regulations";
import { ILocViolationModel, } from "app/system-setup/localization-setup/models/localization-violations";
import {
  LocPenaltyRegulationsDetailsService
} from "app/system-setup/localization-setup/services/localization-penalty-regulations-details.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-penalty-regulations-details",
  templateUrl: "./loc-penalty-regulations-details.component.html",
  styleUrls: ["./loc-penalty-regulations-details.component.scss"],
})
export class LocPenaltyRegulationsDetailsComponent extends AppHelpers implements OnInit {
  @Input() penaltyRegulationId: number;
  selectedPenaltyRegulationDetails: ILocPenaltyRegulationDetailsModel = null;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "violation",
    "penalty",
    "repetition_No",
    "actions",
  ];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<ILocPenaltyRegulationDetailsModel>;


  constructor(
    private penaltyRegulationsDetailsService: LocPenaltyRegulationsDetailsService,
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      Penalty_Regulation_ID: [this.penaltyRegulationId, [Validators.required]],
      violation_ID: [null, [Validators.required]],
      repetition_No: [
        null,
        [Validators.required, CustomValidators.digits, CustomValidators.gte(0)],
      ],
      penalty_ID: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry() && this.penaltyRegulationId > 0) {
      this.getAll();
    }
  }

  override onChangeVersionOrCountry() {
    this.onChangeBusinessAndCompanyId();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.penaltyRegulationsDetailsService
      .getAllByPenaltyRegulationId(this.penaltyRegulationId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ILocPenaltyRegulationDetailsModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isLoaded = true;
          this.dataSource = new MatTableDataSource(
            locPenaltyRegDetailsToMap(res.data)
          );
        }
        else this.errorList(res?.message?.messages);
      });
  }

  penaltyRegulationToEdit(
    penaltyRegulationDetails: ILocPenaltyRegulationDetailsModel
  ) {
    this.form.patchValue(penaltyRegulationDetails);
    this.onChangeViolation([penaltyRegulationDetails.loc_Violation]);
    this.selectedPenaltyRegulationDetails = penaltyRegulationDetails;
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ILocPenaltyRegulationDetailsRequest[] = [
        {
          Penalty_Regulation_ID: this.penaltyRegulationId,
          Violation_ID: this.form.value.violation_ID,
          Repetition_No: this.form.value.repetition_No ?? 0,
          Penalty_ID: this.form.value.penalty_ID,
        },
      ];

      this.selectedPenaltyRegulationDetails
        ? this.update(model)
        : this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ILocPenaltyRegulationDetailsRequest[]) {
    this.showLoading();
    this.penaltyRegulationsDetailsService
      .create(model)
      .subscribe((res: IResponse<ILocPenaltyRegulationDetailsModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: ILocPenaltyRegulationDetailsRequest[]) {
    let editModel: ILocEditPenaltyRegulationDetailsRequest = {
      NewPenalityDetails: model[0],
      OldPenalityDetails: {
        Penalty_Regulation_ID:
        this.selectedPenaltyRegulationDetails.penalty_Regulation_ID,
        Violation_ID: this.selectedPenaltyRegulationDetails.violation_ID,
        Repetition_No: this.selectedPenaltyRegulationDetails.repetition_No ?? 0,
        Penalty_ID: this.selectedPenaltyRegulationDetails.penalty_ID,
      },
    };
    this.showLoading();
    this.penaltyRegulationsDetailsService
      .update(editModel)
      .subscribe((res: IResponse<ILocPenaltyRegulationDetailsModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onDelete(penaltyRegulationDetails: ILocPenaltyRegulationDetailsModel): void {
    let msg = this.msgTranslate(
      `${penaltyRegulationDetails.loc_Violation.ar_Name} - ${penaltyRegulationDetails.repetition_No}`,
      `${penaltyRegulationDetails.loc_Violation.en_Name} - ${penaltyRegulationDetails.repetition_No}`
    );
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(penaltyRegulationDetails);
    });
  }

  delete(penaltyRegulationDetails: ILocPenaltyRegulationDetailsModel) {
    let params: ILocPenaltyRegulationDetailsDeleteParams = {
      Penalty_Regulation_ID: penaltyRegulationDetails.penalty_Regulation_ID,
      Repetition_No: penaltyRegulationDetails.repetition_No,
      Violation_ID: penaltyRegulationDetails.violation_ID,
    };

    this.showLoading();
    this.penaltyRegulationsDetailsService
      .Delete(params)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (
            penaltyRegulationDetails.penalty_Regulation_ID ==
            this.selectedPenaltyRegulationDetails?.penalty_Regulation_ID &&
            penaltyRegulationDetails.violation_ID ==
            this.selectedPenaltyRegulationDetails?.violation_ID &&
            penaltyRegulationDetails.repetition_No ==
            this.selectedPenaltyRegulationDetails?.repetition_No
          )
            this.clearForm();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({Penalty_Regulation_ID: this.penaltyRegulationId});
    this.submitted = false;
    this.selectedViolation = null;
    this.selectedPenaltyRegulationDetails = null;
  }

  selectedViolation: ILocViolationModel = null;

  onChangeViolation(violation: ILocViolationModel[]) {
    this.selectedViolation = violation[0];

    if (this.selectedViolation?.ismoney) {
      this.formCtrls.repetition_No.clearValidators();
      this.formCtrls.penalty_ID.clearValidators();
    }
    else {
      this.formCtrls.repetition_No.setValidators([
        Validators.required,
        CustomValidators.gte(0),
      ]);
      this.formCtrls.penalty_ID.setValidators([Validators.required]);
    }
    this.formCtrls.repetition_No.updateValueAndValidity();
    this.formCtrls.penalty_ID.updateValueAndValidity();
  }
}
