import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocPenaltyRegulationModel,
  ILocPenaltyRegulationRequest,
} from "../models/localization-penalty-regulations";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LocPenaltyRegulationsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILocPenaltyRegulationRequest) {
    return this.post<IResponse<ILocPenaltyRegulationModel>>(
      "PenaltyReg/Create",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocPenaltyRegulationRequest) {
    return this.post<IResponse<ILocPenaltyRegulationModel>>(
      "PenaltyReg/Edit",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ILocPenaltyRegulationModel[]>>(
      "PenaltyReg/GetList",
      {
        params: {
          ...params,
          ...this._paramsService.versionCountryParams,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<ILocPenaltyRegulationModel[]>>("PenaltyReg/GetAll", {
      params: this._paramsService.versionCountryParams,
    });
  }

  getById(penaltyRegulationId: number) {
    return this.get<IResponse<ILocPenaltyRegulationModel[]>>("PenaltyReg/GetAll", {
      params: {
        $filter: `id eq ${penaltyRegulationId}`,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  Delete(Id: number) {
    return this.delete<IResponse>("PenaltyReg/Delete", {
      params: {Id: Id, ...this._paramsService.versionCountryParams},
    });
  }

  exportExcel() {
    this.getAll();
  }
}
