import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocEditPenaltyRegulationDetailsRequest,
  ILocPenaltyRegulationDetailsDeleteParams,
  ILocPenaltyRegulationDetailsModel,
  ILocPenaltyRegulationDetailsRequest,
} from "../models/localization-penalty-regulations";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class LocPenaltyRegulationsDetailsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILocPenaltyRegulationDetailsRequest[]) {
    return this.post<IResponse<ILocPenaltyRegulationDetailsModel>>(
      "PenaltyReg/Create_Penalty_Regulation_Details",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocEditPenaltyRegulationDetailsRequest) {
    return this.post<IResponse<ILocPenaltyRegulationDetailsModel>>(
      "PenaltyReg/Edit_Penalty_Regulation_Details",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getAllByPenaltyRegulationId(penaltyRegulationId: number) {
    return this.get<IResponse<ILocPenaltyRegulationDetailsModel[]>>(
      "PenaltyReg/GetAll_Penalty_Regulation_Details",
      {
        params: {
          Penalty_Regulation_ID: penaltyRegulationId,
          ...this._paramsService.versionCountryParams,
        },
      }
    );
  }

  Delete(params: ILocPenaltyRegulationDetailsDeleteParams) {
    return this.delete<IResponse>(
      "PenaltyReg/Delete_Penalty_Regulation_Details",
      {
        params: params,
        ...this._paramsService.versionCountryParams,
      }
    );
  }
}
