import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { Params } from "@angular/router";
import {
  ILocVacationModel,
  ILocVacationRequest,
  ILocVacationResponse,
} from "app/system-setup/localization-setup/models/localization-vacation";
import { LocVacationService } from "app/system-setup/localization-setup/services/localization-vacation/localization-vacation.service";
import { RadioButtonModel } from "app/shared/components/resources/dynamic-form-field/radio-button/radio-button.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { locVacationToMap } from "app/system-setup/localization-setup/mapper/localization-vacation";

@Component({
  selector: "app-vacation-type",
  templateUrl: "./loc-vacation-type.component.html",
  styleUrls: ["./loc-vacation-type.component.scss"],
})
export class LocVacationTypeComponent
  extends AppHelpers
  implements OnInit
{
  vacationId: number;
  submitted: boolean = false;
  form: FormGroup;

  vacationList: ILocVacationModel[];

  constructor(
    private vacationServ: LocVacationService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<LocVacationTypeComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params: Params) => {
      this.vacationId = params.vacationId ?? 0;
    });
    // if (this.businessId > 0 && this.companyId > 0 && this.vacationId > 0) {
    // 	this.getVactionById();
    // }

    this.form = this.fb.group({
      id: [0, { nonNallable: true }],
      en_Name: [null, [Validators.required]],
      ar_Name: [null, [Validators.required]],
      element_ID: [null],
      monthes_to_get_Balance: [null],
      allow_Post: [false],
      max_Post_Days: [null, [CustomValidators.gte(0)]],
      post_Period_Month: [null, [CustomValidators.gte(0)]],
      offecial_Vaction_Included: [false],
      day_Off_Included: [false],
      isRenew: [false],
      renew_Day: [null, [CustomValidators.gte(0)]],
      renew_Month: [null, [CustomValidators.gte(0)]],
      defaultBalance: [null, [CustomValidators.gte(0)]],
      renew_Period: [null, [CustomValidators.gte(0)]],
      number_of_Earning: [null, [Validators.required, CustomValidators.gte(0)]],
      isDefault: [false],
      is_Paid: [false],
      canBeRequestedToday: [false],
      // maxRelatedBal: [0], //additional property
      deductible_Value: [null, [CustomValidators.gte(0)]],
      relatedVacationId: [null],
      relatedVacationPriority: [null, [CustomValidators.gte(0)]],
      hasAttashment: [false],
      calculate_Balance: [null, [Validators.required]],
      calculate_Balance_By_Contract: [false],
      number_Days_month: [null, [CustomValidators.gte(0)]],
      reportOrder: [null],
      gender: [null],
      isPostedPreviousYears: [false],
      isPostedWithRelatedVacation: [false],
      isEgyptianLaw: [false],
      variableCode: [null, CustomValidator.variableCodeValidators],
      display_In_Result: [false],
    });
    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }
  }

  get formCtrls() {
    return this.form?.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry()) {
      this.getAllVacations();
      if (this.vacationId > 0) this.getVactionById();
    }
  }
  override onChangeVersionOrCountry() {
    this.onChangeBusinessAndCompanyId();
  }
  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: ILocVacationRequest = {
        loc_Vacation: {
          id: this.form.value.id,
          en_Name: this.form.value.en_Name,
          ar_Name: this.form.value.ar_Name,
          element_ID: this.form.value.element_ID,
          monthes_to_get_Balance: this.form.value.monthes_to_get_Balance,
          allow_Post: this.form.value.allow_Post,
          max_Post_Days: this.form.value.max_Post_Days,
          post_Period_Month: this.form.value.post_Period_Month,
          offecial_Vaction_Included: this.form.value.offecial_Vaction_Included,
          day_Off_Included: this.form.value.day_Off_Included,
          isRenew: this.form.value.isRenew,
          renew_Day: this.form.value.renew_Day,
          renew_Month: this.form.value.renew_Month,
          defaultBalance: this.form.value.defaultBalance,
          renew_Period: this.form.value.renew_Period,
          number_of_Earning: this.form.value.number_of_Earning,
          isDefault: this.form.value.isDefault,
          is_Paid: this.form.value.is_Paid,
          canBeRequestedToday: this.form.value.canBeRequestedToday,
          //  maxRelatedBal: this.form.value.maxRelatedBal,
          deductible_Value: this.form.value.deductible_Value,
          relatedVacationId: this.form.value.relatedVacationId,
          relatedVacationPriority: this.form.value.relatedVacationPriority,
          hasAttashment: this.form.value.hasAttashment,
          calculate_Balance: this.form.value.calculate_Balance,
          calculate_Balance_By_Contract:
            this.form.value.calculate_Balance_By_Contract,
          number_Days_month: this.form.value.number_Days_month,
          reportOrder: this.form.value.reportOrder,
          gender: this.form.value.gender,
          isPostedPreviousYears: this.form.value.isPostedPreviousYears,
          isPostedWithRelatedVacation:
            this.form.value.isPostedWithRelatedVacation,
          isEgyptianLaw: this.form.value.isEgyptianLaw,
        },

        variableCode: this.form.value.variableCode,
        display_In_Result: this.form.value.display_In_Result,
      };

      model.loc_Vacation.id == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: ILocVacationRequest) {
    this.showLoading();
    this.vacationServ
      .create(model)
      .subscribe((res: IResponse<ILocVacationResponse>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.vacationId = res.data.loc_Vacation.id;
          this.formCtrls.id.setValue(this.vacationId);
          let url = `/localization-setup/${this.moduleId}/vacations/edit/${this.vacationId}`;
          if (!this.data.isDialog) this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // get element_ID after add
          this.formCtrls.element_ID.setValue(res.data.loc_Vacation.element_ID);
          if (this.data.isDialog) this.dialogRef.close();
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: ILocVacationRequest) {
    this.showLoading();
    this.vacationServ
      .update(model)
      .subscribe((res: IResponse<ILocVacationResponse>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else {
          this.errorList(res?.message?.messages);
          this.hideLoading();
        }
      });
  }

  getVactionById() {
    this.vacationServ
      .getById(this.vacationId)
      .subscribe((res: IResponseList<ILocVacationResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let vacation = locVacationToMap(res.data)[0];
          this.form.patchValue(vacation);
          // this.formCtrls.variableCode.setValue(res.data[0].paY_Variable.code);
          // this.formCtrls.display_In_Result.setValue(res.data[0].paY_Variable.display_In_Result);
        }
      });
  }

  calculateBalance: RadioButtonModel[] = [
    {
      value: 1,
      label: "hireDate",
      cssClass: "col-12 col-md-6",
    },
    {
      value: 2,
      label: "earningDate",
      cssClass: "col-12 col-md-6",
    },
  ];

  onSelectVacation(att_vacation: ILocVacationModel[]) {
    if (att_vacation) {
      let vacation = att_vacation[0];
      if (!vacation) {
        this.formCtrls.renew_Period.enable();
        this.formCtrls.renew_Day.enable();
        this.formCtrls.renew_Month.enable();
      } else {
        this.formCtrls.renew_Period.setValue(vacation.renew_Period);
        this.formCtrls.renew_Day.setValue(vacation.renew_Day);
        this.formCtrls.renew_Month.setValue(vacation.renew_Month);
        this.formCtrls.renew_Period.disable();
        this.formCtrls.renew_Day.disable();
        this.formCtrls.renew_Month.disable();
      }
    }
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }

  getAllVacations() {
    this.vacationServ
      .getAll()
      .subscribe((res: IResponse<ILocVacationResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.vacationList = locVacationToMap(res.data);
        } else this.errorList(res?.message?.messages);
      });
  }
}
