import {
  ILocVacationAgeModel,
  ILocVacationModel,
  ILocVacationResponse,
} from "../models/localization-vacation";

export function locVacationToMap(
  vacations: ILocVacationResponse[]
): ILocVacationModel[] {
  return vacations.map((vacation) => {
    return <ILocVacationModel>{
      ...vacation.loc_Vacation,
      display_In_Result: vacation.loc_Variable?.display_In_Result,
      variableCode: vacation.loc_Variable?.code,
    };
  });
}

export function vacationAgeToMap(
  vacationAges: ILocVacationAgeModel[]
): ILocVacationAgeModel[] {
  return vacationAges.map((vacationAge) => {
    return <ILocVacationAgeModel>{
      ...vacationAge,
      From_Age: vacationAge.from_Age,
      To_Age: vacationAge.to_Age,
    };
  });
}
