import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocVacationAgeModel,
  ILocVacationAgeRequest,
} from "../../models/localization-vacation";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LocVacationAgeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILocVacationAgeRequest) {
    return this.post<IResponse<ILocVacationAgeModel>>(
      "VacationTypeLoc/AddVacationAgeBased",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocVacationAgeRequest) {
    return this.post<IResponse<ILocVacationAgeModel>>(
      "VacationTypeLoc/EditVacationAgeBased",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getList(params: IPageParams, vacationId: number) {
    return this.get<IResponse<ILocVacationAgeModel[]>>(
      "VacationTypeLoc/GetVacationAgeBasedList",
      {
        params: {
          ...params,
          $filter: `vacation_ID eq ${vacationId}`,
          ...this._paramsService.versionCountryParams,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("VacationTypeLoc/DeleteVacationAgeBased", {
      params: { Id: id, ...this._paramsService.versionCountryParams },
    });
  }
}
