import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocVacationInsuranceModel,
  ILocVacationInsuranceRequest,
} from "../../models/localization-vacation";
import { IResponse } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LocVacationInsuranceService extends AppClientService {
  constructor() {
    super();
  }
  create(data: ILocVacationInsuranceRequest) {
    return this.post<IResponse<ILocVacationInsuranceModel>>(
      "VacationTypeLoc/AddVacationInsuranceBased",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocVacationInsuranceRequest) {
    return this.post<IResponse<ILocVacationInsuranceModel>>(
      "VacationTypeLoc/EditVacationInsuranceBased",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getList(params: IPageParams, vacationId: number) {
    return this.get<IResponse<ILocVacationInsuranceModel[]>>(
      "VacationTypeLoc/GetVacationInsuranceBasedList",
      {
        params: {
          ...params,
          ...this._paramsService.versionCountryParams,
          $filter: `vacation_ID eq ${vacationId}`,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>(
      "VacationTypeLoc/DeleteVacationInsuranceBased",
      {
        params: { Id: id, ...this._paramsService.versionCountryParams },
      }
    );
  }
}
