import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  ILocPenaltyRegulationModel,
  ILocPenaltyRegulationRequest,
} from "app/system-setup/localization-setup/models/localization-penalty-regulations";
import { LocPenaltyRegulationsService } from "app/system-setup/localization-setup/services/localization-penalty-regulations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-add-penalty-regulations",
  templateUrl: "./loc-add-penalty-regulations.component.html",
  styleUrls: ["./loc-add-penalty-regulations.component.scss"],
})
export class LocAddPenaltyRegulationsComponent
  extends AppHelpers
  implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  penaltyId: number;


  constructor(private penaltyRegulationsService: LocPenaltyRegulationsService) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.penaltyId = Number(params.get("id")) ?? 0;
    });

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      name: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.penaltyRegulation = null;
    if (
      this.penaltyId > 0 &&
      this.checkNoBusinessOrCompany() &&
      this.checkNoVersionOrCountry()
    )
      this.getById();
  }

  override onChangeVersionOrCountry() {
    this.onChangeBusinessAndCompanyId();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.penaltyRegulationsService
      .getById(this.penaltyId)
      .subscribe((res: IResponse<ILocPenaltyRegulationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.form.patchValue(res.data[0]);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }


  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ILocPenaltyRegulationRequest = {
        ID: this.form.value.id,
        Name: this.form.value.name,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ILocPenaltyRegulationRequest) {
    this.showLoading();
    this.penaltyRegulationsService
      .create(model)
      .subscribe((res: IResponse<ILocPenaltyRegulationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.penaltyId = res.data.id;
          this.formCtrls.id.setValue(this.penaltyId);
          let url = `/localization-setup/${this.moduleId}/penalty-regulations/edit/${this.penaltyId}`;
          this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: ILocPenaltyRegulationRequest) {
    this.showLoading();
    this.penaltyRegulationsService
      .update(model)
      .subscribe((res: IResponse<ILocPenaltyRegulationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    // if (this.penaltyRegulationId == 0)
    this.form.reset({id: 0});
    // else this.penaltyRegulationToEdit();
    this.submitted = false;
  }
}
