import { msgTranslate } from "app/shared/resources/helpers-functions";
import { ILocPenaltyRegulationDetailsModel } from "../models/localization-penalty-regulations";

export function locPenaltyRegDetailsToMap(
  penaltyRegulationDetails: ILocPenaltyRegulationDetailsModel[]
): ILocPenaltyRegulationDetailsModel[] {
  return penaltyRegulationDetails.map((penaltyRegulationDetail) => {
    return <ILocPenaltyRegulationDetailsModel>{
      ...penaltyRegulationDetail,
      violation: msgTranslate(
        penaltyRegulationDetail.loc_Violation?.ar_Name,
        penaltyRegulationDetail.loc_Violation?.en_Name
      ),
      penalty: penaltyRegulationDetail.loc_Penalty?.name,
    };
  });
}
